import React from 'react';
import "./formations.css"

import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button'
import { DataGrid } from '@mui/x-data-grid';
import EndzoneTextInput from '../../../components/form-inputs/text-input/endzone-text-input'
import EndzoneComboBox from '../../../components/form-inputs/endzone-comboBox/endzone-combo-box';

import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import CancelIcon from '@mui/icons-material/Cancel';

import logo from './images/Endzone_Shield.png'

import { Stage, Layer, Rect, Text, Image, Circle, Group} from 'react-konva';

class FormationPage extends React.Component {
    constructor (props) {
        super(props);
        this.httpHandler = require('react-http-client');

        this.state = {
            selected_formation: "New Formation",
            x: 50,
            y: 50,
            formation_players: [],
            finalize_action: "",
            isDraw: false,
            current_WR: 1,
            current_RB: 1,
            current_TE: 1,
            current_formation: "",
            current_image: "",
            rows: []
        }

        this.exportStageRef = React.createRef()

        this.columns = [
            { field: 'Formation', headerName: 'Formation', flex: 1},
            { field: 'RunningBacks', headerName: 'Running Backs', flex: 1},
            { field: 'WideReceivers', headerName: 'Wide Receivers', flex: 1},
            { field: 'TightEnds', headerName: 'Tight Ends', flex: 1},
          ];
    }

    formationSelect = (e) => {
        this.setState(
            {
                selected_formation: e.row.id,
                current_WR: e.row.WideReceivers,
                current_RB: e.row.RunningBacks,
                current_TE: e.row.TightEnds,
                current_formation: e.row.Formation,
                finalize_action: "update"
            }
        )
        if(e.row)
        {
            document.getElementById("formation-input").value = e.row.Formation 
            document.getElementById("rb-input").value = e.row.RunningBacks
            document.getElementById("wr-input").value = e.row.WideReceivers
            document.getElementById("te-input").value = e.row.TightEnds

            document.getElementById("edit-formation").style.display = "inline-flex"
            
            document.getElementById("logo-div").style.display = "none"
            document.getElementById("draw-div").style.display = "none"
            document.getElementById("view-draw-div").style.display = "none"
            document.getElementById("toggle-draw").style.display = "inline-block"

            document.getElementById("discard-formation").style.display = "none"
            document.getElementById("delete-formation").style.display = "inline-flex"
            document.getElementById("get-play-art").style.display = "inline-flex"

            document.getElementById("redraw-formation").style.display = "inline-flex"
            document.getElementById("draw-formation").style.display = "none"
        }
    }

    getImage = () => {
        // Request to database for Image
        // To-Do: Alert if no image found for formation
        const request = this.httpHandler.get(`http://192.168.1.128:80/endzone/utils/formation/image/${this.state.selected_formation}`
        ).then(
            (response) => {
                console.log(response)
                this.setState({current_image: response})
                document.getElementById("logo-div").style.display = "none"
                document.getElementById("toggle-draw").style.display = "none"
                document.getElementById("draw-div").style.display = "none"
                document.getElementById("view-draw-div").style.display = "inline-flex";
            },
            (response) => {
                alert(response)
            }
        );
    }

    getFormations = () => {
        var rows = [];
        this.httpHandler.get("/endzone/utils/formation/get").then((response) => {
            this.setState({rows: response});
        }, (response) => {
            this.setState({
                rows: [
                    { id: 1, Formation: 'Boa', RunningBacks: '2', WideReceivers: "1",   TightEnds: "2"},
                    { id: 2, Formation: 'Trips', RunningBacks: '1', WideReceivers: "3",   TightEnds: "1"},
                    { id: 3, Formation: 'Twins', RunningBacks: '2', WideReceivers: "2",   TightEnds: "1"},
                ]
            })
        })

    }

    newFormation = () => {
        this.setState(
            {
                selected_formation: "New Formation",
                finalize_action: "create"
            }
        )

        document.getElementById("formation-input").value = ""
        document.getElementById("rb-input").value = "1"
        document.getElementById("wr-input").value = "1"
        document.getElementById("te-input").value = "1"
        document.getElementById("edit-formation").style.display = "inline-flex"
        
        document.getElementById("logo-div").style.display = "none"
        document.getElementById("toggle-draw").style.display = "inline-block"
        document.getElementById("draw-div").style.display = "none"
        document.getElementById("view-draw-div").style.display = "none"
        
        document.getElementById("discard-formation").style.display = "inline-flex"
        document.getElementById("delete-formation").style.display = "none"
        
        document.getElementById("get-play-art").style.display = "none"
        document.getElementById("redraw-formation").style.display = "none"
        document.getElementById("draw-formation").style.display = "inline-flex"
    }

    deleteFormation = () => {
        if(window.confirm(`Delete the following formation: ${this.state.current_formation}`))
        {
            this.httpHandler.post("/endzone/utils/formation/delete", {
                id: this.state.selected_formation
            }).then((response) => {
                alert(response)
                this.getFormations()
            }, (request) => {
                var response = JSON.parse(request.message)
                alert(response.data)
            })

        }
    }

    finalizeFormation = () => {
        let formation_name = document.getElementById("formation-input").value;
        let rb_count = document.getElementById("rb-input").value;
        let wr_count = document.getElementById("wr-input").value;
        let te_count  = document.getElementById("te-input").value;
        
        let image = null;
        if(this.state.isDraw){
            image = this.exportStageRef.current.toDataURL();
        }

        // If action == Update
        console.log(this.state.finalize_action)
        if (this.state.finalize_action == "update") {
            this.httpHandler.post("/endzone/utils/formation/update", {
                id: this.state.selected_formation,
                formation: formation_name,
                wideRecievers: wr_count,
                runningBacks: rb_count,
                tightEnds: te_count,
                image: image
            })
        }
        else if (this.state.finalize_action == "create") {
            this.httpHandler.post("/endzone/utils/formation/add", {
                formation: formation_name,
                wideRecievers: wr_count,
                runningBacks: rb_count,
                tightEnds: te_count,
                image: image
            })
        }

        // Finally close it down
        document.getElementById("logo-div").style.display = "inline-flex"
        document.getElementById("toggle-draw").style.display = "none"
        document.getElementById("edit-formation").style.display = "none"
        document.getElementById("draw-div").style.display = "none"
        this.getFormations()
    }

    componentDidMount = () => {
        this.setState({
            x: document.getElementById("drawing-area").clientWidth,
            y: document.getElementById("drawing-area").clientHeight,
            formation_players: []
        })
        this.getFormations()
    }

    reDraw = () => {
        if(window.confirm("Do you want to delete the existing formation art?"))
        {
            this.toggleDraw()
            this.state.isDraw = true
        }
    }

    toggleDraw = () => {
        document.getElementById("draw-div").style.display = "block"
        document.getElementById("toggle-draw").style.display = "none"
        this.state.isDraw = true
    }

    cancelDraw = () => {
        document.getElementById("draw-div").style.display = "none"
        document.getElementById("toggle-draw").style.display = "inline-block"
        this.state.isDraw = false
    }

    render() {
        return(
            <div className='formation-page'>
                <EndzoneNav links={[{title: "Return to Coaches Corner", link: "/endzone/coaches/hub"},]}></EndzoneNav>
                <div className='formation-body-div'>
                    <div className='formation-table-div'>
                        <div className='formation-table-header'>
                            <h4>Edit a formation by clicking on a row in the table. New formations can be added via the button below.</h4>
                        </div>
                        <DataGrid onRowClick={(e) => this.formationSelect(e)} rows={this.state.rows} columns={this.columns} className="table"></DataGrid>
                        <div className='formation-table-footer'>
                            <EndzoneButton text = "Create Formation" onClick = {this.newFormation} icon = {<AddIcon sx = {{"font-size": '150%'}} ></AddIcon>}></EndzoneButton>
                        </div>
                    </div>
                    <div className='formation-body'>
                        <div className='edit-formation' id = "edit-formation">
                            <EndzoneTextInput label = "Formation Name:" id = "formation-input"></EndzoneTextInput>
                            <EndzoneComboBox id = "rb-input" label = "RB Count:" options = {[{"text": "1", "value": "1"}, {"text": "2", "value": "2"}, {"text": "3", "value": "3"}, {"text": "4", "value": "4"}, {"text": "5", "value": "5"}]}></EndzoneComboBox>
                            <EndzoneComboBox id = "wr-input" label = "WR Count:" options = {[{"text": "1", "value": "1"}, {"text": "2", "value": "2"}, {"text": "3", "value": "3"}, {"text": "4", "value": "4"}, {"text": "5", "value": "5"}]}></EndzoneComboBox>
                            <EndzoneComboBox id = "te-input" label = "TE Count:" options = {[{"text": "1", "value": "1"}, {"text": "2", "value": "2"}, {"text": "3", "value": "3"}, {"text": "4", "value": "4"}, {"text": "5", "value": "5"}]}></EndzoneComboBox>
                            <EndzoneButton onClick = {this.finalizeFormation} text = "Finalize Formation" style = {{"fontSize": "1.5vh", "padding": "1vh", "margin": "0vh"}} icon = {<EditIcon sx = {{"font-size": '150%'}} ></EditIcon>} ></EndzoneButton>
                        </div>
                        <div className='toggle-draw' id = "toggle-draw">
                            <div className='btn-div' id = "discard-formation"><button>Discard Formation</button></div>
                            <div className='btn-div' id = "delete-formation"><button onClick={this.deleteFormation}>Delete Formation</button></div>
                            <div className='btn-div' id = "get-play-art"><button onClick={this.getImage}>View Existing Formation Art</button></div>
                            <div className='btn-div' id ="draw-formation"><button onClick={this.toggleDraw}>Draw Formation -- (Needed for Auto Data Entry)</button></div>
                            <div className='btn-div' id ="redraw-formation"><button onClick={this.reDraw}>Re-Draw Formation -- (Deletes Old Drawing)</button></div>
                        </div>
                        <div className='logo-div' id = "logo-div">
                            <img src={logo} className = "load-logo"></img>
                        </div>
                        <div className='draw-div' id = "draw-div">
                            <div className='draw-formation'>
                                <div id = "drawing-area" className='drawing-area'>
                                    <Stage width={700} height={700} ref = {this.exportStageRef}>                                    
                                        <Layer id="draw-space">
                                            {this.state.formation_players.map((item, index) => {
                                                if(item == "RB")
                                                {
                                                    return (
                                                    <Group draggable={true} x={(700 / 2)} y = {(700 / 2)}>
                                                        <Rect width={33} height={33} fill="red"/>
                                                        <Text text = "RB" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                                    </Group>
                                                    )
                                                }
                                                else if (item == "WR")
                                                {
                                                    return (
                                                        <Group draggable={true} x={(700 / 2)} y = {(700 / 2)}>
                                                            <Rect width={33} height={33} fill="blue"/>
                                                            <Text text = "WR" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                                        </Group>
                                                    )
                                                }
                                                else if (item == "TE")
                                                {
                                                    return (
                                                        <Group draggable={true} x={(700 / 2)} y = {(700 / 2)}>
                                                            <Rect width={33} height={33} fill="green"/>
                                                            <Text text = "TE" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                                        </Group>
                                                    )
                                                }
                                            })}
                                            <Group draggable={true} x={(700 / 2 - 16.5)} y = {(700 / 2) - 33}>
                                                <Rect width={33} height={33} fill="teal"/>
                                                <Text text = "QB" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                            </Group>
                                            <Circle width={33} height={33} x={(700 / 2)} y = {(700 / 2) * .825} fill="black" draggable={false}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) + 33} y = {(700 / 2) * .775} draggable={true}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) - 66} y = {(700 / 2) * .775} draggable={true}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) + 79}y = {(700 / 2) * .775} draggable={true}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) - 114} y = {(700 / 2) * .775} draggable={true}/>
                                        </Layer>
                                    </Stage>
                                </div>
                            </div>
                            <div id = "drawing-toolbar" className='drawing-toolbar'>
                                <div className='toolbar-action-div'>
                                    <h2>RB</h2>
                                    <div className='toolbar-actions'>
                                    <button onClick={() => {
                                            let updated_players = [...this.state.formation_players, "RB"]
                                            this.setState({formation_players: updated_players})
                                            document.getElementById("rb-input").value = updated_players.filter((player) => {return player === "RB"}).length;                                                
                                            }
                                        }>+</button>
                                        <button>-</button>                  
                                    </div>
                                </div>
                                <div className='toolbar-action-div'>
                                    <h2>WR</h2>
                                    <div className='toolbar-actions'>
                                        <button onClick={() => {
                                            let updated_players = [...this.state.formation_players, "WR"]
                                            this.setState({formation_players: updated_players})
                                            document.getElementById("wr-input").value = updated_players.filter((player) => {return player === "WR"}).length;                                                
                                            }
                                        }>+</button>
                                        <button>-</button>                  
                                    </div>
                                </div>
                                <div className='toolbar-action-div'>
                                    <h2>TE</h2>
                                    <div className='toolbar-actions'>
                                    <button onClick={() => {
                                            let updated_players = [...this.state.formation_players, "TE"]
                                            this.setState({formation_players: updated_players})
                                            document.getElementById("te-input").value = updated_players.filter((player) => {return player === "TE"}).length;                                                
                                            }
                                        }>+</button>
                                        <button>-</button>                  
                                    </div>
                                </div>
                            </div>
                            <div className='cancel-toolbar'>
                                <EndzoneButton style={{"margin": "1vh"}} text = "Cancel Drawing" onClick = {this.cancelDraw} icon = {<CancelIcon sx = {{"font-size": '150%'}} ></CancelIcon>}></EndzoneButton>
                            </div>
                        </div>
                        <div className='view-draw-div' id = "view-draw-div">
                            <img src = {this.state.current_image}></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



export default FormationPage;